.inputBook {
  width: 100px;
  height: 30px;
  background: green;
}
.bookClassArea {
  position: absolute;
  background-color: white;
}

.closeBookClass {
  display: none;
  opacity: 0;
}
.readHeader {
  font-family: system-ui;
  font-weight: normal;
  margin-top: 100px;
  text-align: center;
}

.nextPage {
  z-index: 10000;
  width: 40vw;
  height: 100vh;
  position: absolute;
  right: 0%;
  top: 0;
}

.prevPage {
  z-index: 10000;
  width: 40vw;
  height: 100vh;
  position: absolute;
  left: 0%;
  top: 0;
}

.closeBook {
  z-index: 10001;
  width: 20px;
  height: 20px;
  cursor: pointer;
  color: #000;
  font-size: 21px;
  text-decoration: none;
  position: absolute;
  top: 1%;
  right: 1%;
}
.containerBook {
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
}
